export class ComponentListService {
    /** */
    static initService(type) {
        type.uuid = ComponentListService.createUUid(type, ComponentListService.collection.length);
        ComponentListService.collection.push(type);
    }
    static createUUid(v, index) {
        return v.tag + "-" + index;
    }
}
ComponentListService.collection = initComponentsConfig.map((v, index) => {
    v.uuid = ComponentListService.createUUid(v, index);
    return v;
});
